import React, { Component } from 'react'

class Button extends Component {
    constructor(props) {
        super(props)
        this.state = {
             started: false
        }
    }
    
    startButton = ()=> {
        if (!this.state.started){
            this.props.startTime()
            this.setState({  started: true })
        }
        this.props.increment()
    }

    resetButton = ()=>{
        this.setState({
            started: false,
        })
        this.props.resetTime()
    }

    render() {
        
        const buttonStyle = {
            padding: 0,
            borderRadius: 20,
            backgroundColor: '#ebebeb',
            width: '85vw',
            height: '65vh',
            fontSize: '5vw',
            fontFamily: 'Verdana',
            borderColor: 'black',
            overflow: 'hidden',
            color: this.props.color,
        
            backgroundImage: `url(${this.props.image})`,
            backgroundSize: 'cover',
            backgroundPosition: 'center',
            backgroundRepeat: 'no-repeat',
        }

        const {runState} = this.props
        return (
            <>
                {runState===2 && 
                <button style={buttonStyle} onClick={this.resetButton}> Restart </button>
                }

                {runState===1 &&
                <button style={buttonStyle} onClick={this.props.increment}></button>                
                }

                {runState===0 &&
                <button style={buttonStyle} onClick={this.startButton}> Start </button>                
                }
            </>
        )
    }
}

export default Button
