import React, { Component } from 'react'
import ButtonDisplay from './ButtonDisplay'

const buttons = [
    {
        displayName: 'Blue',
        name: 'lightblue',
        cost: 2500,
    },
    {
        displayName: 'Gold Lines',
        name: 'goldLines',
        cost: 5000,
    },
    {
        displayName: 'Galaxy',
        name: 'galaxy',
        cost: 7500,
    },
    {
        displayName: 'Cookie',
        name: 'cookie',
        cost: 5000,
    },
    {
        displayName: 'Leaf',
        name: 'leaf',
        cost: 14201,
    },
    {
        displayName: 'Pokeball',
        name: 'pokeball',
        cost: 10000,
    },
]

export class ButtonStore extends Component {

    constructor(props) {
        super(props)
    
        this.state = {
             page: 1
        }
    }

    componentDidMount(){
        this.forceUpdate()
    }

    render() {
        const {page} = this.state
        const currPageButtons = buttons.slice((page-1)*6,page*6).map(button=> 
            <ButtonDisplay 
                key = {button.name} 
                button = {button}
                owned = {this.props.checkIfOwned(button.name)}
                purchaseButton = {this.props.purchaseButton}
                equipButton = {this.props.equipButton}
            />
        )
        return (
            <>
            <div>
                {currPageButtons}
            </div>
            </>
        )
    }
}

export default ButtonStore
