import React, { Component } from 'react'

const buttonStyle = {
    width: '30vw',
    height: '30vh',
    padding: 0,
    borderColor: 'black',
    // border: 0,
    // backgroundColor: 'transparent',
    borderRadius: 10,
    marginTop: '0vh',
    marginLeft: '1vw',
    marginRight: '1vw',
    // marginBottom: '10vh',
    marginBottom: 0,
    // marginLeft: 0,
    // marginRight: 0,
    overflow: 'hidden',
    // objectFit: 'cover'
    // verticalAlign:'middle'
}

const imgStyle = {
    height: '30vh',
    width: '30vmax',
    minWidth: '45vh',
    minHeight: '15vw',
    marginLeft: '-100%',
    marginRight: '-100%',
    marginBottom: '-20%',
    marginTop: '-20%',
}

const spanStyle = {
    float: 'left',
    marginLeft: '2vw',
    marginRight: '-2vw'
}

const textStyle = {
    fontSize: '1.5vmax'
}
class ButtonDisplay extends Component {

    handleClick = ()=>{
        const {button, purchaseButton, owned, equipButton} = this.props
        if (owned===1){
            equipButton(button.name, 'black')
        }
        else if (owned===0) {
            purchaseButton(button.name, button.displayName, button.cost)
        }
        else if (owned!=2){
            alert('Error Loading button')
        }
    }

    render() {
        const {button, owned} = this.props
        
        return (
            <span style = {spanStyle}>
                <button onClick = {this.handleClick} style = {buttonStyle}>
                    <img style = {imgStyle} alt = {button.name} src = {require ('./images/' + button.name +'.jpg')} />
                </button>
                <h1 style = {textStyle} >

                    {owned === 0 && `${button.displayName}: ${button.cost} clicks` }
                    {owned === 1 && `${button.displayName}: Owned` }
                    {owned === 2 && `${button.displayName}: Equipped` }
                    {owned !=0 && owned!=1 && owned !=2 && `${button.displayName}: Loading` }



                </h1>
            </span>

            // <>
            //     <span style = {{float: 'left', marginLeft: '2vw', marginRight: '-2vw'}}> 
            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'lightblue' src = {lightblue} /> </button> 
            //     <h1 >HI</h1>
            //     </span>

            //     <span style = {{float: 'left', marginLeft: '2vw', marginRight: '-2vw'}}> 
            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'goldLines' src = {goldLines} /> </button>
            //     <h1 >HI</h1>
            //     </span>

            //     <span style = {{float: 'left', marginLeft: '2vw', marginRight: '-2vw'}}> 
            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'galaxy' src = {galaxy} /> </button>
            //     <h1 >HI</h1>
            //     </span>

            //     <span style = {{float: 'left', marginLeft: '2vw', marginRight: '-2vw'}}> 
            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'cookie' src = {cookie} /> </button>
            //     <h1 >HI</h1>
            //     </span>

            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'leaf' src = {leaf} /> </button>
            //     <button style = {buttonStyle} > <img style = {imgStyle} alt = 'pokeball' src = {pokeball} /> </button>
            // </>
        )
    }
}

export default ButtonDisplay
