import React, { Component } from 'react'


const buttonStyle = {
    width: '10vw',
    backgroundColor: '#e1e3e6',
    fontFamily: 'Thonburi-Light',
    fontSize: '1.5vw',
    borderRadius: 3,    
}

const textStyleLeft = {
    marginLeft: '1vw',
    marginTop: '1vh',
    fontFamily: 'Thonburi-Light',
    fontSize: '2vw',
    float: 'left'
}

const textStyleRight = {
    marginRight: '1vw',
    marginTop: '1vh',
    fontFamily: 'Thonburi-Light',
    fontSize: '2vw',
    float: 'right'
}

class UserinfoAndShop extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            userID: -1,
            loggedIn: this.props.Username!=null, 
            Username: this.props.Username, 
            clicks: null,
            updatedClicks: false,
            spreadSheetRows: null,
            sheet: null
        }
    }

    async componentDidMount(){
        const {Username} = this.state
        if (Username != null){
            await this.loadUsers()
            this.serachForUser(Username)
        }
    }


    loadUsers = async()=>{
        const {doc} = this.props
        try{
            await doc.loadInfo(); // loads document properties and worksheets
            const sheet = doc.sheetsByIndex[1]; // or use doc.sheetsById[id]
            const rows = await sheet.getRows()
            this.setState({
                spreadSheetRows: rows,
                sheet: sheet
            })

        }
        catch (e){
            this.setState({
                spreadSheetRows: null
            })
        }
    }

    serachForUser = (inputUsername)=>{
        //uses binary search
        const {spreadSheetRows} = this.state
        if (spreadSheetRows === null) {
            return
        }
        let Username = inputUsername.toUpperCase()
        let low = 0; 
        let high = spreadSheetRows[0].totalUsers-1
        

        let index = -1

        while (low <= high){
            let mid = Math.floor((low+high)/2)
            // if (spreadSheetRows[mid] === undefined || spreadSheetRows[mid].User === undefined ){
            //     high = mid-1
            //     continue
            // }

            let compareName = spreadSheetRows[mid].User.toUpperCase()
            if (Username < compareName){
                high = mid-1
            }
            else if (Username > compareName){
                low = mid+1
            }
            else{
                index = mid
                break
            }
        }
        
        if (index === -1){
            //not a user
            const create = window.confirm(`There are currently no accounts with the username ${inputUsername}\nWould you like to create an account with this username?`)
            if (create){
                this.createAccount(inputUsername)
            }
        }
        else{
            //log in
            const ID = spreadSheetRows[index].ID
            if (spreadSheetRows[ID].CurrButton != 0 && this.props.gamePart===0)
                this.props.setButtonImage(require(('./Shop/images/'+ spreadSheetRows[ID].CurrButton + '.jpg')), spreadSheetRows[ID].ButtonColor)

            this.setState({
                userID: ID,
                loggedIn: true, 
                Username: spreadSheetRows[index].User, 
                clicks: spreadSheetRows[ID].Clicks,
                updatedClicks: this.props.runState === 2
            })
            this.props.updateUserName(spreadSheetRows[index].User)

        }
        
    }


    createAccount = async(Username)=>{
        const {sheet} = this.state
        await sheet.addRow({AllUsers: Username, Clicks: 0, CurrButton: 0, ButtonColor: 'black'})
        await this.loadUsers()
        this.serachForUser(Username)
    }

    login = async() =>{
        let Username = prompt('Username:')
        if (Username != null){
            await this.loadUsers()
            this.serachForUser(Username)
        }
    }
    
    loadShop = ()=>{
        // alert("Coming soon in the next update")
        this.props.changeGamePart(1)
    }

    updateClicks = async()=>{
        if (this.state.loggedIn){
            const clicksToAdd = this.props.count
            const {sheet, userID, clicks} = this.state
            const cellAdress = 'C' + (userID*1+2)
            await sheet.loadCells(cellAdress)
            const newClicks = sheet.getCellByA1(cellAdress).value + clicksToAdd
            sheet.getCellByA1(cellAdress).value += clicksToAdd
            this.setState({
                clicks: newClicks,
                updatedClicks: true
            })
            await sheet.saveUpdatedCells()
            // spreadSheetRows[userID].AllClicks = clicks
            // await spreadSheetRows[userID].AllClicks.save()
        }
    }

    resetUpdatedClicks = ()=>{
        this.setState({
            updatedClicks: false
        })
    }

    checkIfOwned = (backgroundName)=>{
        const{userID, spreadSheetRows, loggedIn} = this.state
        if (loggedIn){
            if (spreadSheetRows[userID].CurrButton === backgroundName) return 2

            return (eval ('spreadSheetRows[userID].' + backgroundName) === 'TRUE'? 1: 0 )
        }
        return 0
    }

    purchaseButton = (buttonName, displayName, buttonCost) =>{
        const{loggedIn, clicks} = this.state
        if (loggedIn){
            if (clicks < buttonCost){
                alert("You don't have enough clicks to purchase this button")
            }
            else{
                const buy = window.confirm(`Buy ${displayName} button for ${buttonCost}?`)
                if (buy){
                    this.subtractClicks(buttonCost)
                    this.ownButon(buttonName) 
                }
            }
        }
        else{
            alert('Please log in to buy buttons')
        }
    }

    subtractClicks = async (amount) =>{
        const {sheet, userID} = this.state
        const cellAdress = 'C' + (userID*1+2)
        await sheet.loadCells(cellAdress)
        const newClicks = sheet.getCellByA1(cellAdress).value - amount
        sheet.getCellByA1(cellAdress).value -= amount
        this.setState({
            clicks: newClicks,
        })
        await sheet.saveUpdatedCells()
    }
    ownButon = async (buttonName) =>{
        const {sheet, userID, spreadSheetRows, Username} = this.state
        const cellAdress = eval('spreadSheetRows[0].' + buttonName) + (userID*1+2)
        await sheet.loadCells(cellAdress)
        sheet.getCellByA1(cellAdress).value = true
        await sheet.saveUpdatedCells()
        await this.loadUsers()
        this.serachForUser(Username)
    }

    equipButton = async (buttonName, textColor) =>{
        const {sheet, userID, spreadSheetRows, Username} = this.state
        const CurrButtonAddress = 'D' + (userID*1+2)
        const ButtonColorAddress = 'E' + (userID*1+2)
        await sheet.loadCells(CurrButtonAddress + ':' + ButtonColorAddress)
        sheet.getCellByA1(CurrButtonAddress).value = buttonName
        sheet.getCellByA1(ButtonColorAddress).value = textColor
        await sheet.saveUpdatedCells()
        await this.loadUsers()
        this.serachForUser(Username)
    }


    render() {
        const {loggedIn,Username,clicks, updatedClicks} = this.state
        const {count, gamePart} = this.props
        const accountForUpdatedClicks = updatedClicks? count : 0
        return (
            <>
                <span style = {textStyleLeft}>
                    {loggedIn? 
                        Username
                        : <button onClick = {this.login} style = {buttonStyle}>Login</button>
                    }
                </span>
                <span style = {textStyleRight}>
                    {clicks && 
                        `${ clicks/1+count - accountForUpdatedClicks } clicks `
                    }
                    {gamePart === 0 &&
                        <button onClick = {this.loadShop} style = {buttonStyle}>Shop</button>
                    }
                </span>
            </>
        )
    }


}

export default UserinfoAndShop
