import React, { Component } from 'react'
import Timer from './Timer';
import Button from './Button';
import UserinfoAndShop from './UserinfoAndShop';

const counterStyle = {
    fontFamily: 'Thonburi-Light',
    fontSize: '3vw',
    fontWeight: 'bold'
}

class Game extends Component {
    constructor(props) {
        super(props)
        this.timerRef = React.createRef()
        this.userinfoRef = React.createRef()
        this.state = {
             count: 0,
             runState: 0,
             cheatOn: false,
             spreadSheetRows: null,
             sheet: null,
             buttonImage: null,
             buttonColor: 'black'
            //  0 is haven't started, 1 is running, 2 is stopped
        }
    }

    setButtonImage = (url, color)=>{
        this.setState({buttonImage:url, buttonColor: color})
    }

    componentDidMount(){
        this.loadInfo()
    }

    loadInfo = async()=>{
        const {doc} = this.props
        try{
            await doc.loadInfo(); // loads document properties and worksheets
            const sheet = doc.sheetsByIndex[0]; // or use doc.sheetsById[id]
            const rows = await sheet.getRows()
            this.setState({
                spreadSheetRows: rows,
                sheet: sheet
            })
        }
        catch{
            this.setState({
                spreadSheetRows: null
            })
        }
    }    
    
    toggleCheat = ()=>{
        this.setState((prevState)=>({
            cheatOn: !prevState.cheatOn
        }))
    }

    increment = () =>{
        if (this.state.cheatOn){
            this.setState((prevState) => ({
                count: prevState.count + 1.3
            }))
        }
        else{
            this.setState((prevState) => ({
                count: prevState.count + 1
            }))
        }
    }

    updateState = (newState)=>{
        this.setState({
            runState: newState
        })
    }

    endGame = ()=>{
        this.userinfoRef.current.updateClicks()
        this.alertScore()
    }

    alertScore = ()=>{
        const {spreadSheetRows} = this.state
        let count = Math.floor(this.state.count)
        let score = count/5
        let message
        if (count < 15){
            message = "You're about as fast as a Sanjay. SMH slowpoke"
        }
        else if (count < 30){
            message = 'Bruh my grandma could click faster and she has arthritis'
        }
        else if (count < 45){
            message = "Mehh, it's alright I guess?"
        }
        else if (count<60){
            message = "Damn your fast. Want a cookie?"
        }
        else{
            message = "Good job! You deserve a cookie!"
        }
        message += `\n\nYou clicked ${count} times in 5 seconds!\nThat's a whopping ${score} clicks per second!\n`
        message += '\nHigh Scores:\n'
        let added = false
        for (let i = 0; i<5; i++){
            if (spreadSheetRows==null) {
                message+='\nError: Could not load high scores'
                break
            }
            if (!added && score > spreadSheetRows[i].Score){
                added = true
                let userName = prompt(`Congrats your score of ${score} is high enough for the leader boards!\n\nName:`)
                this.addScore(userName,score,i)
            }
            message+= `\n${i+1}. ${spreadSheetRows[i].Name}: ${spreadSheetRows[i].Score} CPS`
        }
        message+='\n'
        alert(message)
    }

    addScore = async(userName, userScore, row)=>{
        const {spreadSheetRows,sheet} = this.state

        for (let j = 3; j>=row; j--){
            spreadSheetRows[j+1].Name = spreadSheetRows[j].Name
            spreadSheetRows[j+1].Score = spreadSheetRows[j].Score
        }
        spreadSheetRows[row].Name = userName
        spreadSheetRows[row].Score = userScore
        this.setState({
            spreadSheetRows: spreadSheetRows
        })
        await sheet.addRow({ AllNames: userName, AllScores: userScore})
    }

    startTime = () => {this.timerRef.current.startTime()}
    
    resetTime = ()=> {
        this.loadInfo()
        this.timerRef.current.resetTime()
        this.setState({ 
            count: 0,
            cheatOn: false
        }) 
        //runState is updated by the timer resetTime
        this.userinfoRef.current.resetUpdatedClicks()
    }
    
    render() {
        const {runState, count, buttonImage, buttonColor} = this.state
        const {doc, Username, updateUserName, changeGamePart} = this.props
        return (
            <>
            <UserinfoAndShop
                doc = {doc}
                count = {count}
                runState = {runState}
                ref = {this.userinfoRef}
                gamePart = {0}
                setButtonImage = {this.setButtonImage}
                changeGamePart = {changeGamePart}
                updateUserName = {updateUserName}
                Username = {Username}
            />
            <Timer
                ref = {this.timerRef}
                updateState = {this.updateState}
                endGame = {this.endGame}
                toggleCheat = {this.toggleCheat}
            />
            <div style = {counterStyle}> Count: {Math.floor(count)} </div>
            <Button  
                startTime={this.startTime} 
                resetTime={this.resetTime} 
                increment={this.increment}
                runState={runState}
                bool = {true}
                image = {buttonImage}
                color = {buttonColor}
            />

            </>
        )
    }
}

export default Game
