import React, { Component } from 'react'

var incAmount = 40;

const timerStyle = {
    fontSize: '7vw',
    tableLayout: "fixed",
    color: 'white',
    marginLeft: 'auto',
    marginRight: 'auto',
    marginTop: '0',
    fontFamily: 'Thonburi-Light',
}

class Timer extends Component {
    constructor(props) {
        super(props)
    
        this.state = {
            ms: 0, 
            s:0, 
            m: 0,
            colonClickNum: 0
        }
    }

    startTime = () =>{
        this.myInterval = setInterval( ()=>{
            this.calculateNewStates()
        }, incAmount)
        this.props.updateState(1)
    }

    stopTime = () =>{
        clearInterval(this.myInterval)
        this.props.updateState(2)
    }

    resetTime = () =>{
        this.setState({
            ms: 0, 
            s:0, 
            m: 0,
            colonClickNum: 0
        })
        this.props.updateState(0)
    }
    
    calculateNewStates = () =>{
        const {ms,s,m} = this.state
        let mNew = m
        let sNew = s
        let msNew = ms+incAmount
        if (msNew>=1000){
            sNew++
            msNew-=1000
        }
        if (sNew>=60){
            mNew++
            sNew-=60
        }

        this.setState({
            ms: msNew,
            s: sNew,
            m: mNew
        })

        if (sNew===5 && msNew>0){
            this.setState({ms: 0})
            this.stopTime()
            this.props.endGame()
        }
    }

    handleClickColon = ()=> {
        this.setState( (prevState) => ({
            colonClickNum: prevState.colonClickNum + 1
        }),
        ()=>{
            if (this.state.colonClickNum === 5){
                this.props.toggleCheat()
            }
            else if (this.state.colonClickNum === 6){
                this.props.toggleCheat()
            }
        })
    }

    render() {
        const {ms, s, m} = this.state
        return (
            <table style = {timerStyle} ><thead><tr>
                <th style={{ width: '3.5vw' }}> {m<10? '0'+m : m} </th>
                <th style={{ width: '3.5vw' }}>:</th>
                <th style={{ width: '3.5vw' }}> {s<10? '0'+s : s} </th>
                <th onClick={this.handleClickColon} style={{ width: '3.5vw' }}>:</th>
                <th style={{ width: '3.5vw' }}> {ms<100? '0'+ms/10 : ms/10} </th>
            </tr></thead></table>
        )
    }
}

export default Timer
