import React, { Component } from 'react'
import UserinfoAndShop from '../UserinfoAndShop'
import ButtonStore from './ButtonStore'


const returnButtonStyle = {
    width: '12vw',
    minHeight: '7vh',
    backgroundColor: '#e1e3e6',
    fontFamily: 'Thonburi-Light',
    fontSize: '2vmax',
    // borderBottomLeftRadius: 14,
    borderBottomRightRadius: 14,
    marginBottom: 0,
}
const buttonStyle = {
    width: '44vw',
    minHeight: '7vh',
    backgroundColor: '#e1e3e6',
    fontFamily: 'Thonburi-Light',
    fontSize: '2vmax',
    borderBottomLeftRadius: 14,
    borderBottomRightRadius: 14,
    marginBottom: 0
}

class Storefront extends Component {


    constructor(props) {
        super(props)
        this.userinfoRef = React.createRef()
        this.state = {
             store: 0
             //0 is button, 1 is backgrounds
        }
    }
    
    returnToGame = () =>{
        this.props.changeGamePart(0)
    }

    loadBackgrounds = () =>{
        alert('This will be available in the next update')
        this.setState({store:0})
    }

    checkIfOwned = (backgroundName) =>{
        try{
            return this.userinfoRef.current.checkIfOwned(backgroundName)
            //0 does not own, 1 owns button, 2 equipped
        }
        catch { 
        } //loading
    }

    purchaseButton = (buttonName, displayName, buttonCost) =>{
        this.userinfoRef.current.purchaseButton(buttonName, displayName, buttonCost)
    }

    equipButton = (buttonName, textColor) =>{
        this.userinfoRef.current.equipButton(buttonName, textColor)
    }


    render() {
        const{doc, updateUserName, Username} = this.props
        return (
            <>
            <div>
                <button onClick = {this.returnToGame} style ={returnButtonStyle}>Return</button>
                <button style ={buttonStyle}>Buttons</button>
                <button onClick = {this.loadBackgrounds} style = {buttonStyle}>Backgrounds</button>
            </div>
            <div style = {{
                    // marginBottom: '10vh',
                    height: '10vh',
                    minHeight: '4.5vw'
                }}>
                <UserinfoAndShop
                    gamePart = {1}
                    ref = {this.userinfoRef}
                    doc = {doc}
                    count = {0}
                    updateUserName = {updateUserName}
                    Username = {Username}
                />
            </div>
            
            <ButtonStore
                checkIfOwned = {this.checkIfOwned}
                purchaseButton = {this.purchaseButton}
                equipButton = {this.equipButton}
            />
            </>
        )
    }
}

export default Storefront